import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import '@devsnc/sn-record-control-wrapper'; // still use control-wrapper because of description
import '@servicenow/now-radio-buttons';

const COMPONENT_NAME = 'now-radio-group';

const view = (state, { dispatch }) => {
	const { properties, componentId } = state;
	const {
		options,
		name,
		readonly,
		vertical,
		invalid,
		value: selectedValue
	} = properties;
	const readOnly = isAttrTrue(readonly);
	const layout = isAttrTrue(vertical) ? 'vertical' : 'horizontal';
	const radioOptions = options.map(o => ({
		id: o.value,
		label: o.displayValue,
		checked: o.value === selectedValue
	}));
	return (
		<sn-record-control-wrapper description={properties.description} fieldset>
			<now-radio-buttons
				{...properties}
				layout={layout}
				options={radioOptions}
				manageValue
			/>
		</sn-record-control-wrapper>
	);
};

/**
 * A group of radio buttons that allows a user to select one radio button from the group
 * similar to a group of native HTML `input` element of type radio.
 *
 * ```jsx
 * const options = [{displayValue: 'Red', value: 'red'}, {displayValue: 'Red', value: 'blue'}];
 *
 * <now-radio-group options={options} />
 *
 * <now-radio-group value='red' options={options} />
 *
 * ```
 *
 * @seismicElement now-radio-group
 * @summary A group of radio buttons that allows the user to select one element from the group.
 *
 * */
createCustomElement(COMPONENT_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * Provide a description for the radio group
		 * @type {string}
		 */
		description: {},
		/**
		 * Mark selected option as invalid
		 * @type {boolean}
		 */
		invalid: { default: false },
		/**
		 * The label for the radio group
		 * Does not need to be unique
		 * @type {string}
		 */
		label: {},
		/**
		 * User's preferred language
		 * @type {string}
		 */
		language: {},
		/**
		 * Name of the group, must be unique
		 * if there are multiple groups on a page
		 * @type {string}
		 */
		name: {},
		/**
		 * An array of objects representing the
		 * available options in the radio group
		 * each containing a displayValue to be shown
		 * and an associated value
		 * @type {Array<{displayValue: string, value: any}>}
		 */
		options: { default: [] },
		/**
		 * Enable to show the selected radio button
		 * but prevent hover and click functionality
		 * @type {boolean}
		 */
		readonly: { default: false },
		/**
		 * Enable to mark radio group as a required field
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * The value associated with the
		 * selected radio button
		 * @type {any}
		 */
		value: {},
		/**
		 * If true, uses vertical layout
		 * If false, uses horizontal layout
		 * Not currently supported
		 * @type {boolean}
		 * @private
		 */
		vertical: { default: true }
	},
	actionHandlers: {
		'NOW_RADIO_BUTTONS#VALUE_SET': {
			effect: ({ state, action, dispatch }) => {
				dispatch('NOW_RADIO_GROUP#VALUE_CHANGED', {
					name: state.properties.name,
					value: action.payload.value
				});
			},
			stopPropagation: true
		}
	},
	dispatches: {
		/**
		 * Dispatched when a radio button is clicked.
		 * @type {{name: string, value: any}}
		 */
		'NOW_RADIO_GROUP#VALUE_CHANGED': {}
	}
});

export default COMPONENT_NAME;
